import "./orderForm.scss";
import { Formik, Form } from "formik";
import { object, string, number } from "yup";
import "yup-phone";
import TextField from "../../components/TextField";
import { useSelector, useDispatch } from "react-redux";
import { productsInBasket } from "../../store/basket/reducer";
import { useNavigate } from "react-router-dom";
import { deleteFromBasket } from "../../store/basket/actions";
import Button from "../../components/Button";

const validate = object({
  firstName: string()
    .matches(/^[aA---\s]+$/, "    ")
    .max(15, "'     15 ")
    .required("' "),
  lastName: string()
    .matches(/^[aA---\s]+$/, "    ")
    .max(20, "     20 ")
    .required("' "),
  age: number()
    .typeError("   ")
    .positive("   ' ")
    .max(99, "  99")
    .integer("    ")
    .required("' "),
  address: string()
    .max(50, "    50 ")
    .required("' "),
  phone: string()
    .required("' ")
    .phone(null, true, "    +380   12 "),
});

export default function OrderForm() {
  const basket = useSelector(productsInBasket);
  const dispatch = useDispatch();
  const navigate = useNavigate();

  const handleSubmit = (values) => {
    console.log("  :", values);
    basket.forEach((product) => {
      console.log(
        `: ${product.name} (${product.color}) - ${product.qty} .`
      );
      dispatch(deleteFromBasket(product.sku, true));
    });
    navigate("/thanks-for-order");
  };

  return (
    <Formik
      initialValues={{
        firstName: "",
        lastName: "",
        age: "",
        address: "",
        phone: "",
      }}
      validationSchema={validate}
      onSubmit={handleSubmit}
    >
      {(formik) => {
        return (
          <section className="order__form">
            <h3 className="order__form__title">
                   :
            </h3>
            <Form>
              <TextField label="'" name="firstName" type="text" />
              <TextField label="" name="lastName" type="text" />
              <TextField label="" name="age" type="text" />
              <TextField label=" " name="address" type="text" />
              <TextField
                label=" "
                name="phone"
                type="text"
                placeholder="+38(099)-999-99-99"
              />
              <Button className="order__form__btn" type="submit">
                 
              </Button>
            </Form>
          </section>
        );
      }}
    </Formik>
  );
}
